@import url(https://fonts.googleapis.com/css2?family=Overpass+Mono:wght@300;400&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  font-family: "Overpass Mono", monospace;
  font-weight: 400;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Overpass Mono", monospace;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(90deg, rgb(5, 58, 82), rgb(51, 54, 56));
  /* linear-gradient(90deg, #a1c4fd, #c2e9fb); */
  overflow: hidden;
}

.container {
  width: 80vw;
  min-height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  background: linear-gradient(90deg, #333, #222);
  box-shadow: 0 0 10px black, 0 0 10px black;
  border-radius: 30px;
  transition: all 0.2s;
  -webkit-animation: come-in 2s;
          animation: come-in 2s;
}

@-webkit-keyframes come-in {
  0% {
    transform: translateY(1000px);
  }
  30% {
    transform: translateY(400px);
  }
  70% {
    transform: translateY(500px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes come-in {
  0% {
    transform: translateY(1000px);
  }
  30% {
    transform: translateY(400px);
  }
  70% {
    transform: translateY(500px);
  }
  100% {
    transform: translateY(0);
  }
}

#result {
  display: flex;
  width: auto;
  flex-wrap: wrap;
  justify-content: center;
}

#password,
#copy,
#generate {
  margin: 10px;
  text-align: center;
  padding: 5px 20px;
  box-shadow: 0 0 3px black, 0 0 3px black;
  border-radius: 5px;
}

#length {
  margin: 5px;
  text-align: center;
  padding: 5px 5px;
  box-shadow: 0 0 3px black, 0 0 3px black;
  border-radius: 5px;
}

#password {
  width: 80%;
  padding: 10px 30px;
}

#options {
  display: flex;
  flex-wrap: wrap;
  color: white;
  flex-direction: column;
}

.option {
  margin: 15px 10px;
}

@media screen and (min-width: 1000px) {
  .container {
    width: 70vw;
    font-size: 1.2rem;
  }

  #result {
    margin-right: 40px;
  }

  #password {
    width: 100%;
  }
}

